@use './theme/colors';
@use './theme/fonts';
@use './theme/fx';
@use './theme/mixins';

footer{
    background-color: colors.$black;
    a{
        font-family: fonts.$p;
    text-decoration:none;
    font-size: fonts.$font-size;
    color: colors.$white;
    }
    &a:hover{
        color: colors.$white;
        opacity: 1;
        }
}

.footer{
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction:column;
    margin:0 1rem 0 1rem;
    &__logo{
        margin:3rem;
        width:20%;
    }
    &__logo:hover{
        width: 35%;
        filter: brightness(0) contrast(100%)invert(77%) sepia(48%) saturate(711%) hue-rotate(354deg) brightness(104%) contrast(87%);
    }
    &__contact{
        flex-grow: 1;
        flex-basis: 0;
        & img{
            width: 2.5rem;
            height: 2.5rem;
            &:hover{
                filter: brightness(0) contrast(100%)invert(77%) sepia(48%) saturate(711%) hue-rotate(354deg) brightness(104%) contrast(87%);
            }
        }
    }
    &__owner{
        flex-grow: 1;
        flex-basis: 0;
        &  a:hover, .footer__contact p:hover{
            opacity: 0.5;
        }
    }
}

.footer__social{
    display:flex;
    flex-direction: row;
    align-items:center;
    justify-content: space-between;
    width: 100%;
    & img{
        width: 2.5rem;
        height: 2.5rem;
        margin: 0 0.25rem 0 0.25rem;
        filter: invert(88%) sepia(83%) saturate(7499%) hue-rotate(327deg) brightness(95%) contrast(96%);
        &:hover{
            width:4rem;
            height:4rem;
            filter: brightness(0%) invert(1);
        }
    }
}

.contact-link__footer{
    display:flex;
    flex-direction: row;
    align-items:center;
    justify-content:flex-end;
}

.phone-number__text:before {
    content: "+x (xxx) xxx xxxx";
}
.phone-number:hover .phone-number__text:before {
    content: "+1 (581) 985 0167";
}

.email__text:before {
    content: "Nous contacter";
}
.email:hover .email__text:before {
    content: "info@anthonylemay.com";
}

.footer-coords{
    display:flex;
    width:100%;
    flex-direction: row;
    align-items:center;
    justify-content: space-between;
    padding: 1rem;
}

.footer__contact__item__element{
    margin: 1rem;
}

.nav-link__footer{
    display:flex;
    flex-direction: row;
    padding-bottom: 1rem;
    margin-bottom: 1rem;
    width: 100%;
}

footer ul li .nav-link-item__footer {
    color: colors.$white;
    font-family: fonts.$h;
    text-decoration:none;
    font-size: 1.2rem;
    margin: 1rem;
}

footer ul li .nav-link-item__footer:hover{
    color: colors.$yellow;
}

@media screen and (max-width: 1100px){
 
    .nav-link__footer{
        flex-wrap: wrap;
        justify-content: center;
    }
    footer ul li{
       margin: 1rem 0 1rem 0;
    }
    
    .footer__logo{
        width:30%;
    }
    

}




@media screen and (max-width: 995px){
 
    .footer-coords{
        flex-direction: column;
    }

    .footer__owner{
        margin-bottom: 2rem;
    }
    

}

@media screen and (max-width: 600px){
 
    .contact-link__footer{
        justify-content: space-around;
    }
    

}


